import { _decorator, Button, Component, instantiate, Node, Prefab, Animation, Label } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('shoot')
export class shoot extends Component {
    
    @property(Prefab)
    bulletPrefab: Prefab = null;

    @property(Node)
    enemy: Prefab = null;

    @property(Label)
    hit: Prefab = null;




    // @property(Button)
    // shoot_btn: Button=null;

    start() {
        // this.shoot_btn.node.on('click', this.onclickshoot,this);
    }

    update(deltaTime: number) {

    }

    onclickshoot(evt){
        //確認按鍵有吃到此函數
        console.log("按下射擊");
        //複製bulletPrefab(子彈)
        let addbullet = instantiate(this.bulletPrefab);
        //在場景中新增節點
        this.node.addChild(addbullet);

        let bulletAni = addbullet.getComponent(Animation);
        bulletAni.on(Animation.EventType.FINISHED, () => {
            addbullet.parent = null;
        }, this);

        this.hittime(addbullet, this.enemy);
    }

    collision(addbullet, enemy){
        let dist = addbullet.position.sub(enemy.position).mag();
        return dist;

    }

    hittime(addbullet, enemy){
        if(this.collision(addbullet, enemy) < 10){
            addbullet.parent = null;
        }
    }




}


